import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import axios from "axios";
import echarts from 'echarts';
import { Message } from 'element-ui';

Vue.prototype.$echarts = echarts

Vue.config.productionTip = false

Vue.use(ElementUI);

Vue.prototype.$axios = axios;
axios.defaults.baseURL = 'https://acfly.cn/background';
/*
axios.defaults.baseURL = 'http://borui.cn.utools.club';
*/

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

//请求拦截器
axios.interceptors.request.use((config) => {
  /*store.commit('showLoading');*/
  if (localStorage.token) { //判断token是否存在
    config.headers.token = localStorage.token;  //将token设置成请求头
  }
  return config;
}, function (error) {
  /*store.commit('hideLoading');*/
  return Promise.reject(error);
});

//响应拦截器
axios.interceptors.response.use((config) => {
  /*store.commit('hideLoading');*/
  const data = config.data;
  const code = data.state;
  if(code === false) {
    console.log('token过期');
    /*alert("登录已过期，请重新登录后再访问主页");*/
    //获取cookie
    let username = getCookie('username');
    let password = getCookie('password');
    if(username && password) {
      console.log("自动重新登录");
      login(username, password);
    }else {
      console.log("cookie中的数据不存在");
      location.href = '#/login'
    }
  }
  return data;
}, function (error) {
  return Promise.reject(error);
});

function getCookie(name) {
  var reg = RegExp(name + '=([^;]+)');
  var arr = document.cookie.match(reg);
  if (arr) {
    return arr[1];
  } else {
    return '';
  }
}

//设置cookie
function createCookie(name, value, day) {
  var date = new Date();
  date.setDate(date.getDate() + day);
  document.cookie = name + '=' + value + ';expires='+ date;
}

function login(username, password) {
  axios.post('/user/manager', {
    username: username,
    password: password
  }).then((res) => {
    if (res.status === 200) {
      let data = res.data;
      localStorage.setItem("token", data.token);
      localStorage.setItem("expired", data.expired);
      createCookie('username',username,7);
      createCookie('password',password,7);
      /*router.push('/index/statistics');*/
      Message.success('已重新登录成功！');
    } else {
      this.$message.error(res.msg);
    }
  }).catch((err) => {
    console.log(err);
  })
}
