import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
/*  {
    path: '*',
    redirect: '/index/statistics'
  },*/
  {
    path: '/',
    redirect: '/index/statistics'
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/login/login'),
    meta: {
      needLogin: false
    }
  },
  {
    path: '/index',
    name: 'Index',
    component: () => import('../views/index'),
    meta: {
      needLogin: true
    },
    children: [
      {
        path: 'statistics',
        name: 'Statistics',
        component: () => import('../views/statistics/statistics'),
        meta: {
          needLogin: true
        }
      },
      {
        path: 'order',
        name: 'Order',
        component: () => import('../views/order/order'),
        meta: {
          needLogin: true
        }
      },
      {
        path: 'resource',
        name: 'Resource',
        component: () => import('../views/resource/resource'),
        meta: {
          needLogin: true
        }
      },
      {
        path: 'commodity',
        name: 'Commodity',
        component: () => import('../views/commodity/commodity'),
        meta: {
          needLogin: true
        }
      },
      {
        path: 'customer',
        name: 'Customer',
        component: () => import('../views/customer/customer'),
        meta: {
          needLogin: true
        }
      },
      {
        path: 'system',
        name: 'System',
        component: () => import('../views/system/system'),
        meta: {
          needLogin: true
        }
      }
    ]
  },
]

const router = new VueRouter({
  routes,
  mode: 'hash'
})

router.beforeEach(function(to, from, next) {
  if (to.meta.needLogin) {
    if (localStorage.getItem("token")) {
      next();
    } else {
      next({
        path: '/login'
      });
      alert('请登录后再访问主页！')
    }
  } else {
    next();
  }
});

export default router
